<template>
  <div class="Mvideo">
    <div class="mv-tab">
      <van-tabs
        swipeable
        v-model="activeName"
        title-active-color="#eee"
        @change="getMvTypeData"
      >
        <van-tab
          v-for="(item, index) in mvTypeData"
          :name="item.title"
          :title="item.title"
          :key="index"
        >
          <lazy-component>
            <div
              class="mv-item"
              v-for="(item, index) in MvData"
              :key="index"
              id="index"
            >
              <!-- <video
                
                autoplay
                ref="mvvideo"
                v-show="index == mvindex"
                class="mvvideo auto-img"
                :src="mvurldata.url"
                @click.stop="tabplay(index)"
              ></video> -->
              <div v-show="index != mvindex" class="mv-img-box">
                <img v-lazy="item.cover" class="auto-img" />
                <span class="mv-playcount"
                  ><van-icon name="music-o" />{{
                    item.playCount | million
                  }}</span
                >
                <img
                  src="../../assets/images/mv-play.png"
                  class="auto-img mv-play"
                />
                <span class="mv-time"
                  ><van-icon name="underway-o" />{{
                    item.duration | timeFormatter
                  }}</span
                >
              </div>
              <div class="mv-contnet">{{ item.name }}</div>
              <div class="mv-foot">{{ item.artistName }}</div>
            </div>
          </lazy-component>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import "../../assets/less/video.less";
export default {
  data() {
    return {
      activeName: "推荐",
      mvTypeData: [
        {
          title: "推荐",
        },
        {
          title: "内地",
        },
        {
          title: "港台",
        },
        {
          title: "欧美",
        },
        {
          title: "日本",
        },
        {
          title: "韩国",
        },
      ],
      MvData: [],
      mvurldata: [],
      playmv: false,
      mvurl: "",
      mvindex: -1,
      // getmvdata:[],
    };
  },
  filters: {
    //  处理播放量
    million: function (value) {
      //播放量数值处理
      let num;
      if (value > 99999999) {
        num = Math.floor(value / 10000000) / 10 + "亿";
      } else if (value > 9999) {
        num = Math.floor(value / 1000) / 10 + "万";
      } else if (value < 9999 && value > -9999) {
        num = value;
      }
      return num;
    },
    // 处理播放时长
    timeFormatter: function (duration) {
      let secondTime = parseInt(duration) / 1000; // 秒
      let minuteTime = 0; // 分
      let hourTime = 0; // 小时
      if (secondTime > 60) {
        // 如果秒数大于60，将秒数转换成整数
        // 获取分钟，除以60取整数，得到整数分钟
        minuteTime = parseInt(secondTime / 60);
        // 获取秒数，秒数取佘，得到整数秒数
        secondTime = parseInt(secondTime % 60);
        // 如果分钟大于60，将分钟转换成小时
        if (minuteTime > 60) {
          // 获取小时，获取分钟除以60，得到整数小时
          hourTime = parseInt(minuteTime / 60);
          // 获取小时后取佘的分，获取分钟除以60取佘的分
          minuteTime = parseInt(minuteTime % 60);
        }
      }
      let result = secondTime > 9 ? secondTime : "0" + secondTime;
      if (minuteTime > 0) {
        result =
          (minuteTime > 9 ? minuteTime : "0" + minuteTime) + ":" + result;
      } else {
        result = "00" + ":" + result;
      }
      if (hourTime > 0) {
        result = (hourTime > 9 ? hourTime : "0" + hourTime) + ":" + result;
      } else {
        result = "00" + ":" + result;
      }
      return result;
    },
  },
  computed: {
    recommendType: function () {
      return this.activeName;
    },
    // getmvdata: function(){
    //   return this.$refs.mvvideo
    // }
  },
  created() {
    this.getMvTypeData();
    // this.gettext();

  },

  methods: {
    //暂停/播放
    // tabplay(index){
    //   if (this.getmvdata[index].paused) {
    //     this.getmvdata[index].play()
    //   }else{
    //     this.getmvdata[index].pause()
    //   }
    // },
    //播放mv
    // playmvvideo(item, index) {

    //   this.mvindex = index;

    //   this.gettext(item.id);
    // },
    //测试端口
    // gettext(mid) {
    //   this.axios({
    //     method: "GET",
    //     url: "/mv/url",
    //     params: {
    //       id:mid,
    //     },
    //   })
    //     .then((result) => {
    //       if (result.data.code === 200) {
    //         this.mvurldata = result.data.data;
    //       }
    //     })
    //     .catch((err) => {
    //       console.error(err)
    //     });
    // },

    getMvTypeData() {
      this.mvindex = -1,
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/mv/all",
        params: {
          area: this.recommendType,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) { 
            this.MvData = result.data.data;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
  },
};
</script>